const baseUrl = "https://nhyhtapi.zkhuashui.com/"

function request(url,data,way){//requset

	
	data = data || {},
	
	uni.showToast({//loading
	    title: '',
	    duration: 200,
		icon:"loading"
	});
	return new Promise((resolve,rejext)=>{
		uni.request({//uniappapi
		    url: baseUrl + url, //
		    data,
			method:way||"POST",//POST
		    success: (res) => {
				resolve(res)
		    }
		});
	})
}

function showToast(options){//Toast
	let { title,duration,icon } = options;
	uni.showToast({
	    title:title,
	    duration: duration,
		icon:icon || "none"
	});
}


module.exports = {
	request,
	baseUrl,
	showToast
}
